<template>
  <div class="main">
    <Heareds title="企业入驻" :method="state" @leftBtn="leftBtn" />
    <div class="contents">
      <div class="warnimg">
        <p>
          <img src="./images/warning.png" alt />
          <span class="title" v-if="show">
            如果企业还没有入驻,
            <span style="color:#4373ea;" @click="dj">点击这里</span>入驻
          </span>
          <span class="title" v-if="!show">带“*”号为必填，不带可以跳过不填写。</span>
        </p>
      </div>
    </div>
    <div class="content" v-if="show">
      <van-search placeholder="请搜索企业名称，完成绑定" v-model="keywords" shape="round" />
      <div class="icon">
        <p>
          <img src="./images/1.png" alt />
        </p>
        <span style="margin-top:0.426667rem">
          <span>
            企业未入驻,您可以进行
            <span style="color:#4373ea;" @click="dj">企业入驻</span>
          </span>
        </span>
      </div>
    </div>
  </div>
</template>
<script>
import Heareds from "@/components/Header";
export default {
  data() {
    return {
      keywords: "",
      show: true,
      state: true
    };
  },
  components: {
    Heareds
  },
  methods: {
    dj() {
      this.state = this.show = false;
      
    },
    leftBtn() {
      this.state = this.show = true;
    }
  }
};
</script>
<style lang="less" scoped>
.main {
  background: #f4f4f5;
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  top: 0;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
  .contents {
    margin-top: 1.173666rem;
  }
  .warnimg {
    background: rgba(67, 115, 234, 0.2);
    height: 0.96rem;
    line-height: 0.96rem;
    font-size: 0.373333rem;
    position: relative;
    img {
      width: 0.4rem;
      height: 0.4rem;
      position: absolute;
      left: 0.426667rem;
      top: 50%;
      margin-top: -0.186667rem;
    }
    .title {
      margin-left: 0.96rem;
      padding: 0;
    }
  }
}
.icon {
  text-align: center;
  p {
    margin-top: 40%;
    img {
      width: 2.746667rem;
      height: 2.826667rem;
      margin: 0 auto;
      // background-image: url(static/img/noClient.e2ee165.png);
      // background-size: 100%;
    }
  }
  span {
    font-size: 0.453333rem;
    display: inline-block;
    margin-top: 0.426667rem;
    color: #7d7e7f;
  }
}
</style>


